@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
#main{
    font-family: "Roboto", sans-serif;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;

}
#main .section-content{
    height: 100%;
    flex:1 ;
    padding: 20px;
    border: 1px solid #f1f5f9;
    border-radius: 20px;
    box-shadow:  0px 2px 5px 7px rgba(141, 138, 138, 0.16);
}
#main .section-content #content{
    flex: 1;
    height: 100%;
    width: 100%;
    overflow: auto;
    min-height: calc(100vh - 23vh);
}
#main .section-content h1{
    font-size: 2em;
    font-weight: bolder;
    text-align: center;
    color: transparent;

}
#main .section-content #table {
    border-collapse: collapse;
    margin: auto;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
}
#table tbody{
    font-size: 12px;

}
#table tbody tr:not(.heading) td,
#table tbody tr:not(.header) td
{
    font-size: 12px;
    font-weight: bold;

}
#table tbody tr.heading{
    font-size: 13px;
    font-weight: 700;
}
#table tbody tr.heading td{ 
    padding: 8px 0;
    font-weight: bolder;

}
#table tbody tr.header td{
    font-size: 13px;
    font-weight: 700;
    text-align: center;
background-color:#f1f5f9;

    /* color: #cc720b; */
}
#table tbody tr:not(.heading) td{
    border: 1px solid #64748b;
    padding: 0 10px;
}

#table tbody tr.first td,
#table tbody tr.first td{
    
}
#main .section-content h1.first{
    background: #4E47CF;
    background: linear-gradient(to left, #bd792c 0%, #e68210 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}

#main .section-content h1.second{
    background: #4E47CF;
    background: linear-gradient(to left, #bd792c 0%, #e68210 100%);
-webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}
